@font-face {
  font-family: 'unna';
  src: url('assets/fonts/unna-regular-webfont.eot');
  src: local('☺'), url('assets/fonts/unna-regular-webfont.eot?#iefix') format('embedded-opentype'),
    url('assets/fonts/unna-regular-webfont.woff2') format('woff2'),
    url('assets/fonts/unna-regular-webfont.woff') format('woff'),
    url('assets/fonts/unna-regular-webfont.ttf') format('truetype'),
    url('assets/fonts/unna-regular-webfont.svg#unnaregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
